.input-row {
  width: 60%;
  display: flex;
  flex-direction: column;
  align-items: start;
  margin: 0 auto 10px;
  position: relative;
}

.input-row label {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #1E1E1E;
  opacity: 0.8;
}

.input-row input {
  width: 100%;
  padding: 15px 20px;
}

.password-toggle {
  top: 70%!important;
}

.button-row {
  max-width: 430px;
  margin: 110px auto 50px;
  display: flex;
  justify-content: flex-end;
}

