<template>
  <div class="my-confirm-modal">
    <div class="my-login-modal-content" @click.stop>
      <h2>Подтверждение</h2>
      <p v-if="!confirmationCode">Не пришло сообщение? Можно <span :class="this.active" @click="moreCode">прислать ещё</span> через {{ timer }} секунд</p>
      <form @submit.prevent="confirm">
        <div class="my-login-modal-rows">
          <div class="my-login-modal-row">
            <input type="text" id="login" v-model="code" required placeholder="Код"/>
          </div>
        </div>

        <div class="btn-block">
          <button class="custom-button back-btn" type="button" @click="close('confirm')">Отмена</button>
        </div>
        <div class="btn-block">
          <button class="custom-button" type="submit">Войти</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      code: '',
      active: '',
      timer: 90,
    };
  },
  created() {
    const timer = setInterval(() => {
      this.timer = this.timer - 1
    }, 1000)

    setTimeout(() => {
      this.active = 'active-code'
      clearInterval(timer)
    }, 90000)
  },
  props: ['close', 'signInWithVerificationCode', 'confirmationCode'],
  methods: {
    confirm() {
      if (this.confirmationCode) {
        this.signInWithVerificationCode(this.code)
        return
      }

      const data = {
        email: this.$store.getters.getEmail,
        code: this.code,
      }

      this.$store.dispatch('emailVerify', data)
    },
    moreCode() {
      if (this.confirmationCode) {
        console.log('code')
        return
      }

      const data = {
        email: this.$store.getters.getEmail,
      }

      this.$store.dispatch('sendCode', data)
    }
  }
};
</script>

