<template>
  <div>
    <div v-if="!isImageExpanded" class="thumbnail-container">
      <div v-for="(image, index) in images" :key="index" class="thumbnail-wrapper">
        <img
            :src="image"
            :alt="'Image ' + (index + 1)"
            @click="expandImage(index)"
        />
      </div>
    </div>

    <div v-else class="expanded-image-container" @click="shrinkImage">
      <img :src="images[expandedImageIndex]" :alt="'Image ' + (expandedImageIndex + 1)" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isImageExpanded: false,
      expandedImageIndex: 0,
    };
  },
  props: ['images'],
  methods: {
    expandImage(index) {
      this.isImageExpanded = true;
      this.expandedImageIndex = index;
    },
    shrinkImage() {
      this.isImageExpanded = false;
    },
  },
};
</script>

<style scoped>
.thumbnail-container {
  display: flex;
  max-width: 882px;
}

.thumbnail-wrapper {
  width: 160px;
}

.thumbnail-container img {
  width: 100%;
  height: auto;
}

.thumbnail-wrapper {
  margin: 5px; /* Add some margin between thumbnails */
}

.expanded-image-container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 9999;
  cursor: pointer;
}

.expanded-image-container img {
  height: 80%;
  object-fit: cover;
  border-radius: 19px;
}
</style>