<template>
  <div class="container my-bids-con">
    <div class="my-bids-buttons">
      <button type="button" :class="active === 1 ? 'bid-active' : ''" @click="check(1)">Мои ставки</button>
      <button type="button" :class="active === 2 ? 'bid-active' : ''" @click="check(2)">Статус ставки</button>
    </div>
    <div class="row row-cols-md-2 custom-cards" v-if="active === 1">
      <div class="car-card car-card-v2 mb-4 custom-card" v-for="car in cars.results" :key="car">
        <div class="row custom-card-in">
          <h3 class="car-title">{{ car.brand + ' ' + (car.model ? car.model : '') }}</h3>
          <div class="col-md-5 p-0">
            <div class="car-thumbnail">
              <img :src="car.image" :alt="car.brand" v-if="car.image">
            </div>
          </div>
          <div class="col-md-7">
            <div class="car-body">
              <span class="car-auction-date">Дата аукциона: <b>{{
                  String(car.auction_date).replace(' ', 'г.,') + 'ч.'
                }}</b></span>
              <p class="group_data">
                <span class="car-price mb-3">Моя ставка: <b> ${{ car.price }}</b></span>
                <span class="car-vin">VIN: <b>{{ car.vin ? car.vin : '-' }}</b></span>
              </p>
              <div class="row car-info">
                <div class="col-md-4 col-6 mb-3">
                  <i class="mileage"></i>
                  <b>{{ car.mileage ? car.mileage : '0' }} км</b>
                  <span>Пробег</span>
                </div>
                <div class="col-md-4 col-6 mb-3">
                  <i class="year"></i>
                  <b>{{ car.year ? car.year : '-' }}</b>
                  <span>Год</span>
                </div>
                <div class="col-md-4 col-6 mb-3">
                  <i class="transmission"></i>
                  <b>{{ car.transmission ? car.transmission : '-' }}</b>
                  <span>КПП</span>
                </div>
                <div class="col-md-4 col-6 mb-3">
                  <i class="fuel"></i>
                  <b>{{ car.fuel ? car.fuel : '-' }}</b>
                  <span>Топливо</span>
                </div>
                <div class="col-md-4 col-6 mb-3">
                  <i class="volume"></i>
                  <b>{{ car.volume ? car.volume : '-' }}</b>
                  <span>Двигатель</span>
                </div>
                <div class="col-md-4 col-6 mb-3">
                  <i class="body"></i>
                  <b>{{ car.body ? car.body : '-' }}</b>
                  <span>Кузов</span>
                </div>
              </div>
              <div class="car-footer">
                <div class="car-footer-wrap">
                  <div class="car-flag-wrap">
                    <img :src="car.flag" :alt="car.auction">
                    <b>{{ car.auction }}</b>
                  </div>
                </div>
                <router-link :to="'/detail/' + car.id" class="car-btn" target="_blank">Подробнее</router-link>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="pag-list" v-if="cars?.previous !== null || cars?.next !== null">
        <a class="pag-link pag-arrow-left" :class="cars.previous === null && 'disabled'"
           v-on:click="paginationCars(cars.previous)">Предыдущая</a>
        <a class="pag-link pag-arrow-right" :class="cars.next === null && 'disabled'"
           v-on:click="paginationCars(cars.next)">Следующая</a>
      </div>
      <div class="box-wrap" v-if="!(cars?.results?.length !== 0)">
        <h3 class="wrap-title text-center">Ничего не найдено</h3>
      </div>
    </div>
    <div class="my-bids-data" v-if="active === 2">


      <div class="my-bids-table" v-if="bids?.results?.length">
        <table>
          <thead>
          <tr>
            <th>лот номер</th>
            <th>Год выпуска</th>
            <th>марка</th>
            <th>Модель</th>
            <th>VIN</th>
            <th>Моя ставка</th>
            <th>продано</th>
            <th>статус</th>
            <th>Детали</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="item in bids?.results" :key="item">
            <td>№{{ item.lot_number }}</td>
            <td>
              {{ item.year }}
            </td>
            <td>{{ item.brand }}</td>
            <td>{{ item.model }}</td>
            <td>{{ item.vin }}</td>
            <td>${{ item.bid_price }}</td>
            <td>{{ item.price ? '$' + item.price : '---' }}</td>
            <template v-if="item.status_bid === 'pending'">
              <td>
