<script>
export default {
  data() {
    return {
      active: 1
    }
  },
  created() {
    this.$store.dispatch('getBidCars')
    this.$store.dispatch('getBidData')
  },
  computed: {
    cars() {
      return this.$store.getters.getBidCars
    },
    bids() {
      return this.$store.getters.getBidData
    }
  },
  methods: {
    check(value) {
      this.active = value
    },
    paginationCars(url) {
      this.$store.dispatch('getBidCars', url)
    },
    paginationBids(url) {
      this.$store.dispatch('getBidData', url)
    }
  }
}
</script>

